import React from "react";
import {useSelector} from "react-redux";
import {makeStyles} from "@material-ui/core/styles";
import CreateDocumentIcon from "../../assets/icons/CreateDocumentIcon";
import Grid from "@material-ui/core/Grid";
import Button from "@material-ui/core/Button";
import {documentSelector} from "../../selectors";
import DocumentList from "./DocumentList";
import {createDocument} from "../../AC/create-document";
import {useDispatch} from "react-redux";
import {Droppable} from "react-beautiful-dnd";

const useStyles = makeStyles((theme) => ({
  panel: {
    "width": `100%`,
    "padding": `25px 50px 25px 25px`,
    "marginBottom": `20px`,
    "background": `#fafafa`,
    "boxSizing": `border-box`,
    "borderRadius": `3px`,
    "boxShadow": `0px 1px 3px 0px rgba(0,0,0,0.2),
                0px 1px 1px 0px rgba(0,0,0,0.14),
                0px 2px 1px -1px rgba(0,0,0,0.12)`,
    "maxHeight": `420px`,
    "overflowY": `auto`,
    "&::-webkit-scrollbar": {
      width: `5px`,
      backgroundColor: `#f5f5f5`
    },
    "&::-webkit-scrollbar-track": {
      backgroundColor: `#f0f4f7`
    },
    "&::-webkit-scrollbar-thumb": {
      backgroundColor: `#ccc`,
      borderRadius: `5px`
    }
  },
  heading: {
    fontWeight: theme.typography.fontWeightRegular
  },
  button: {
    borderRadius: `80px`
  },
  center: {
    textAlign: `center`
  }
}));

export default function Document() {
  const {content} = useSelector(documentSelector);
  const classes = useStyles();
  const dispatch = useDispatch();
  const handleClick = () => {
    dispatch(createDocument);
  };
  return (
    <Grid container>
      <Grid item xs={12}>
        <Droppable droppableId="document">
          {(provided) => (
            <div
              ref={provided.innerRef}
              {...provided.droppableProps}
              className={classes.panel}
            >
              <DocumentList provided={provided} />
            </div>
          )}
        </Droppable>
      </Grid>
      {content.length ? (
        <Grid item xs={12} className={classes.center}>
          <Button
            variant="contained"
            aria-label="Create"
            color="primary"
            size="large"
            className={classes.button}
            onClick={handleClick}
          >
            <CreateDocumentIcon />
            Create
          </Button>
        </Grid>
      ) : null}
    </Grid>
  );
}
