import React from "react";
import Paper from "@material-ui/core/Paper";
import Button from "@material-ui/core/Button";
import ArrowIcon from "@material-ui/icons/ArrowRightAltOutlined";
import {useSelector, useDispatch} from "react-redux";
import {modulesSelector} from "../../selectors";
import {makeStyles} from "@material-ui/core/styles";
import {Grid} from "@material-ui/core";
import {Droppable} from "react-beautiful-dnd";
import NoModules from "./NoModules";
import ModuleList from "./ModuleList";
import {moveSelectedModulesToDocument} from "../../AC/move-selected-modules-to-document";

const useStyles = makeStyles(() => ({
  button: {
    borderRadius: `80px`
  },
  droppable: {
    "maxHeight": `420px`,
    "overflowY": `auto`,
    "marginBottom": `20px`,
    "&::-webkit-scrollbar": {
      width: `5px`,
      backgroundColor: `#f5f5f5`
    },
    "&::-webkit-scrollbar-track": {
      backgroundColor: `#f0f4f7`
    },
    "&::-webkit-scrollbar-thumb": {
      backgroundColor: `#ccc`,
      borderRadius: `5px`
    }
  }
}));

function Modules() {
  const classes = useStyles();
  const {content} = useSelector(modulesSelector);
  const dispatch = useDispatch();
  const handleClick = () => {
    dispatch(moveSelectedModulesToDocument);
  };

  return content.length ? (
    <>
      <Droppable droppableId="modules">
        {(provided) => (
          <div
            className={classes.droppable}
            ref={provided.innerRef}
            {...provided.droppableProps}
          >
            <Paper className={classes.paper}>
              <ModuleList provided={provided} />
            </Paper>
          </div>
        )}
      </Droppable>
      <Grid container justify="flex-end">
        <Grid item>
          <Button
            variant="outlined"
            aria-label="Add"
            color="primary"
            size="large"
            onClick={handleClick}
            className={classes.button}
          >
            Add to document
            <ArrowIcon />
          </Button>
        </Grid>
      </Grid>
    </>
  ) : (
    <NoModules />
  );
}

export default Modules;
