import {GET_MODULES, START, SUCCESS, FAIL} from "../constants";
import initialStore from "../store/initial-store";

export const initStore = (dispatch) => {
  dispatch({
    type: GET_MODULES + START
  });
  if (window.google) {
    window.google.script.run
        .withSuccessHandler((modules) => {
          dispatch({
            type: GET_MODULES + SUCCESS,
            payload: {
              modules
            }
          });
        })
        .withFailureHandler((error) => {
          dispatch({
            type: GET_MODULES + FAIL,
            error
          });
        })
        .getModules();
  } else {
    dispatch({
      type: GET_MODULES + SUCCESS,
      payload: {
        modules: initialStore.modules.content
      }
    });
  }
};
