import {START, SUCCESS, FAIL, CREATE_DOCUMENT} from "../constants";

export const createDocument = (dispatch, getState) => {
  const {
    document: {content}
  } = getState();
  dispatch({
    type: CREATE_DOCUMENT + START
  });
  window.google.script.run
      .withSuccessHandler(() => {
        dispatch({
          type: CREATE_DOCUMENT + SUCCESS
        });
      })
      .withFailureHandler((error) => {
        dispatch({
          type: CREATE_DOCUMENT + FAIL,
          error
        });
      })
      .createRunbook(content);
};
