import React, {useState} from "react";
import PropTypes from "prop-types";
import {useDispatch} from "react-redux";
import ExpansionPanel from "@material-ui/core/ExpansionPanel";
import ExpansionPanelSummary from "@material-ui/core/ExpansionPanelSummary";
import ExpansionPanelDetails from "@material-ui/core/ExpansionPanelDetails";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import Grid from "@material-ui/core/Grid";
import TextField from "@material-ui/core/TextField";
import MenuItem from "@material-ui/core/MenuItem";
import {makeStyles} from "@material-ui/core/styles";
import IconButton from "@material-ui/core/IconButton";
import Box from "@material-ui/core/Box";
import NotificationIcon from "@material-ui/icons/NotificationImportantOutlined";
import DeleteIcon from "@material-ui/icons/DeleteOutlineOutlined";
import {changeModuleTitle} from "../../AC/change-module-title";
import {changeModuleVariable} from "../../AC/change-module-variable";
import {removeModuleFromDocument} from "../../AC/remove-module-from-document";

const useStyles = makeStyles(() => ({
  root: {
    "&:after": {
      content: `" "`,
      width: `50px`,
      height: `100%`,
      position: `absolute`,
      left: `100%`,
      top: `0`,
      zIndex: `0`
    }
  },
  textField: {
    width: `100%`,
    marginBottom: `8px`
  },
  hiddenTextField: {
    "&:hover": {
      "& fieldset": {
        borderWidth: `1px`
      }
    }
  },
  fieldset: {
    borderWidth: 0
  },
  input: {
    padding: `9px 14px`
  },
  disableFocused: {
    margin: 0,
    background: `none !important`
  },
  icon: {
    color: `#E93333`,
    position: `absolute`,
    right: `-50px`,
    top: 0,
    zIndex: 1
  },
  notificationIcon: {
    color: `#ffab00`,
    margin: `10px 3px -5px 0`
  },
  box: {
    background: `#f5f5f5`
  }
}));

const Panel = (props) => {
  const classes = useStyles();
  const dispatch = useDispatch();
  const [deleteButtonIsShow, showDeleteButton] = useState(false);
  const {uuid, title, variables} = props;
  const changeTitle = (id) => (e) => {
    dispatch(changeModuleTitle(id, e.target.value));
  };
  const changeVariable = (id, name) => (e) => {
    dispatch(changeModuleVariable(id, name, e.target.value));
  };
  const handleClick = (id) => () => {
    dispatch(removeModuleFromDocument(id));
  };
  return (
    <ExpansionPanel
      {...props}
      ref={props.innerRef}
      onMouseEnter={() => showDeleteButton(true)}
      onMouseOver={() => showDeleteButton(true)}
      onMouseLeave={() => showDeleteButton(false)}
      className={classes.root}
    >
      <ExpansionPanelSummary
        expandIcon={<ExpandMoreIcon />}
        aria-controls={`panel-${uuid}-content`}
        id={`panel-${uuid}-header`}
        className={classes.disableFocused}
        classes={{content: classes.disableFocused}}
      >
        <TextField
          variant="outlined"
          required
          error={title ? false : true}
          helperText={title ? `` : `This field must be filled`}
          id="outlined-name"
          type="text"
          onChange={changeTitle(uuid)}
          className={classes.hiddenTextField}
          value={title}
          InputProps={{
            classes: {
              input: classes.input,
              notchedOutline: classes.fieldset
            }
          }}
        />
        {deleteButtonIsShow ? (
          <IconButton className={classes.icon} onClick={handleClick(uuid)}>
            <DeleteIcon />
          </IconButton>
        ) : null}
      </ExpansionPanelSummary>
      <ExpansionPanelDetails
        className={variables && variables.length ? `` : classes.box}
      >
        <Grid container>
          {variables && variables.length ? (
            variables.map(({type, name, value, options, required}) => (
              <Grid item xs={12} key={name}>
                {type !== `options` ? (
                  <TextField
                    id={`outlined-text-${name}`}
                    required={required}
                    label={name}
                    type={type}
                    multiline={type === `textarea`}
                    className={classes.textField}
                    value={value}
                    onChange={changeVariable(uuid, name)}
                    variant="outlined"
                  />
                ) : (
                  <TextField
                    id={`outlined-select-${name}`}
                    select
                    label={name}
                    className={classes.textField}
                    value={value}
                    onChange={changeVariable(uuid, name)}
                    variant="outlined"
                  >
                    {options.map((option) => (
                      <MenuItem key={option.value} value={option.value}>
                        {option.label}
                      </MenuItem>
                    ))}
                  </TextField>
                )}
              </Grid>
            ))
          ) : (
            <Box fontSize={14}>
              <NotificationIcon className={classes.notificationIcon} />
              This module doesn&#39;t have any variables. If you don&#39;t know
              how to add variables in your module you can see that{` `}
              <a href="#" target="_blank">
                here.
              </a>
            </Box>
          )}
        </Grid>
      </ExpansionPanelDetails>
    </ExpansionPanel>
  );
};
Panel.propTypes = {
  innerRef: PropTypes.func,
  uuid: PropTypes.string,
  title: PropTypes.string,
  variables: PropTypes.array
};

export default Panel;
